import { Deserializable } from "./deserializable";

export class Pet implements Deserializable {
    id: string;
    name: string;
    gender: string;
    age: string;
    neutered: string;
    vaccinated: string;
    size: string;
    description1: string;
    //description2: string;
    image: string;
    cartImage: string;
    petCategory: string;
    price: number;
    adopted: boolean;

    deserialize(input: any): this {
        Object.assign(this, input);
        return this;
    }
}

